.ui-btn {
  display: inline-block;
  vertical-align: middle;
  height: 71px;
  line-height: 71px;
  max-width: 100%;
  position: relative;
  border: none;
  padding: 0px 60px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: #FFF;
  color: #23372B;
  text-align: center;
  font-size: 22px;
  font-size: 1.375rem;
  letter-spacing: 1.6px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  transition: box-shadow 0.2s, transform 0.2s;
  box-shadow: 14px 14px 0px #5E6738;
}
@media (max-width: 768px) {
  .ui-btn {
    box-shadow: 10px 10px 0px #5E6738;
  }
}
.ui-btn svg {
  transform: translate(0, 20%);
  margin-right: 8px;
}
@media (max-width: 1140px) {
  .ui-btn svg {
    width: 18px;
    height: 18px;
  }
}
@media (max-width: 768px) {
  .ui-btn svg {
    width: 12px;
    height: 12px;
    margin-right: 5px;
  }
}
@media (max-width: 599px) {
  .ui-btn svg {
    width: 20px;
    height: 20px;
  }
}
.ui-btn:hover {
  box-shadow: none;
  transform: translate(0px, 1px);
}
.ui-btn:active {
  transform: translate(0px, 2px);
}
@media (max-width: 1180px) {
  .ui-btn {
    font-size: 16px;
    font-size: 1rem;
    height: 50px;
    line-height: 50px;
    padding: 0px 33px;
  }
}
@media (max-width: 768px) {
  .ui-btn {
    font-size: 20px;
    font-size: 1.25rem;
    height: 64px;
    line-height: 60px;
    padding: 0px 60px;
  }
}

