.screens {
  max-width: 2560px;
  margin: 0 auto;
  max-height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}

.screen {
  scroll-snap-align: start;
}

.screen-promo {
  height: 100vh;
  max-height: 1366px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  background-color: #538c57;
}
.screen-promo-content {
  max-width: 358px;
  display: flex;
  flex-direction: column;
}
.screen-promo-slogan img {
  max-height: 56vh;
}
@media (max-width: 1180px) {
  .screen-promo-slogan img {
    max-height: 46vh;
  }
}
@media (max-width: 992px) {
  .screen-promo-slogan img {
    max-height: 46vh;
  }
}
@media (max-width: 599px) {
  .screen-promo-slogan img {
    max-height: 30vh;
  }
}
.screen-promo-slogan-mobile {
  visibility: hidden;
  display: none;
}
.screen-promo-btn {
  margin: 0 auto;
}
@media (max-width: 768px) {
  .screen-promo-btn {
    position: absolute;
    bottom: 40px;
    z-index: 3;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
.screen-promo-bg {
  position: absolute;
  z-index: -2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
}
.screen-promo-bottles {
  position: absolute;
  z-index: -1;
  left: 34%;
  top: 0;
  max-height: 100%;
  max-width: 85%;
  display: block;
}
@media (min-aspect-ratio: 4/3) {
  .screen-promo-bottles {
    max-width: 60%;
  }
}
@media (orientation: portrait) {
  .screen-promo-bottles {
    max-width: 100%;
    left: 0;
  }
}
@media (max-width: 1440px) {
  .screen-promo-content {
    max-width: 260px;
  }
}
@media (max-width: 1180px) {
  .screen-promo-content {
    max-width: 200px;
  }
}
@media (max-width: 992px) {
  .screen-promo {
    height: calc(var(--vh, 1vh) * 100);
  }
  .screen-promo-content {
    max-width: 275px;
  }
  .screen-promo-bottles {
    right: -5%;
    max-height: 80%;
  }
}
@media (max-width: 599px) and (max-aspect-ratio: 1/1.75) {
  .screen-promo {
    padding-top: 13vh;
  }
  .screen-promo-bottles {
    left: -5%;
    right: -10%;
    max-width: 115%;
  }
}
@media (max-width: 768px) {
  .screen-promo-logo {
    display: none;
    visibility: hidden;
  }
  .screen-promo-content {
    margin: 0 auto;
    max-width: 100%;
    text-align: center;
    margin-top: 65vh;
  }
  .screen-promo-slogan {
    top: 30px;
    right: 30px;
    position: absolute;
    z-index: 2;
  }
  .screen-promo-slogan-mobile {
    visibility: visible;
    display: block;
    font-size: 42px;
    font-size: 2.625rem;
    line-height: 1.2;
    color: #FFF;
    position: absolute;
  }
  .screen-promo-slogan-mobile img {
    max-width: 46%;
    display: block;
    margin-left: auto;
  }
  .screen-promo-bottles {
    top: 50%;
    right: unset;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 390px) {
  .screen-promo {
    min-height: 550px;
  }
}

.screen-alert {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 20px;
  right: 5px;
  width: calc(100vw - 40px);
  max-width: 1680px;
  z-index: 1;
}
@media (max-width: 599px) {
  .screen-alert {
    visibility: hidden;
    display: none;
  }
}
.screen-alert.mobile {
  visibility: hidden;
  display: none;
  z-index: -1;
}
@media (max-width: 599px) {
  .screen-alert.mobile {
    visibility: visible;
    display: block;
  }
}

.screen-benefits {
  background-color: #23372B;
  position: relative;
  overflow: hidden;
}
@media (max-width: 992px) {
  .screen-benefits .screen-alert {
    display: none;
    visibility: hidden;
  }
}

.benefits-slide {
  overflow: hidden;
  position: relative;
  background-color: #23372B;
}
.benefits-slide-cols {
  display: flex;
}
.benefits-slide-img, .benefits-slide-content {
  flex: 0 0 50%;
  max-width: 50%;
  height: 100vh;
  max-height: 1366px;
}
.benefits-slide-img {
  position: relative;
}
.benefits-slide-img-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.benefits-slide-content {
  padding: min(16vh, 160px) 120px 0 150px;
  background: url(../images/bottom-screen-bg.png) right center no-repeat;
  background-size: auto 100%;
}
.benefits-slide-inner {
  max-width: 870px;
  margin: 0 auto;
}
.benefits-slide-descr {
  font-size: 48px;
  font-size: 3rem;
  overflow-y: auto;
  color: #F3E8D4;
  padding-right: 40px;
  font-weight: 500;
}
.benefits-slide-descr > :last-child {
  margin-bottom: 0;
}
.benefits-slide-descr::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: #9F1A2A;
}
.benefits-slide-descr::-webkit-scrollbar-thumb {
  width: 4px;
  height: 4px;
  background-color: #fff;
}
.benefits-slide-where {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, 0);
  max-width: 50%;
  max-height: calc(100% - 70px);
}
@media (max-width: 1680px) {
  .benefits-slide-inner {
    max-width: 420px;
  }
  .benefits-slide-content {
    padding-left: 80px;
  }
  .benefits-slide-descr {
    padding-right: 25px;
  }
}
@media (max-width: 1366px) {
  .benefits-slide-img {
    flex: 0 0 42%;
    max-width: 42%;
  }
  .benefits-slide-content {
    flex: 0 0 58%;
    max-width: 58%;
    padding-left: 45px;
  }
  .benefits-slide-where {
    left: 42%;
    max-width: 16%;
  }
  .benefits-slide-inner {
    max-width: 380px;
  }
}
@media (max-width: 1180px) {
  .benefits-slide-where {
    max-width: 14.5%;
  }
}
@media (max-width: 992px) {
  .benefits-slide-cols {
    display: block;
  }
  .benefits-slide-img {
    max-width: 100%;
    height: 75vh;
  }
  .benefits-slide-content {
    max-width: 100%;
    height: auto;
    padding: 40px 40px 120px;
  }
  .benefits-slide-title {
    font-size: 36px;
    font-size: 2.25rem;
    margin-bottom: 20px;
  }
  .benefits-slide-where {
    display: none;
    visibility: hidden;
  }
  .benefits-slide-descr {
    max-height: 114px;
    padding-right: 15px;
  }
