@media (max-width: 768px) {
  .benefits-slide-content {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (max-width: 599px) {
  .benefits-slide-content {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 30px;
